<template>
  <div id="fabric-card-editable-component">
    <div
      class="cover-image"
      :style="{
        backgroundImage: `url('${formatImageUrl(data.fabric.image_url)}@250-250')`
      }"
      @click="onImageClick"
    >
      <div class="top-console">
        <div class="button-wrapper" @click.stop="onPlusClick">
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'plus']"
          ></font-awesome-icon>
        </div>

        <div class="button-wrapper" @click.stop="onEditClick">
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'edit']"
          ></font-awesome-icon>
        </div>

        <el-popconfirm
          confirmButtonText="Confirm"
          cancelButtonText="Cancel"
          confirmButtonType="success"
          cancelButtonType="info"
          :confirm="onDeleteConfirm"
          hideIcon
          title="Are you sure you want to delete?"
        >
          <template #reference>
            <div class="button-wrapper">
              <font-awesome-icon
                class="icon"
                :icon="['fas', 'trash-alt']"
              ></font-awesome-icon>
            </div>
          </template>
        </el-popconfirm>
      </div>
      <div class="view-container">
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'eye']"
        ></font-awesome-icon>
        <p>0</p>
      </div>
      <div class="cover"></div>
    </div>
    <div class="content-container">
      <div class="content-wrapper">
        <h3 class="title">{{ data.fabric.name }}</h3>
        <p class="description">{{ data.fabric.real_composition }}</p>
      </div>
      <div class="price-group">
        <p class="price">$ {{ data.price }}</p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";

import { formatImageUrl } from "@/utils/image";
import { CollectionFabrics } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCardEditable",
  props: {
    data: {
      type: Object as PropType<CollectionFabrics>,
      default: () => ({})
    }
  },
  setup(props, context) {
    const router = useRouter();

    const { data } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onPlusClick = () => {
      context.emit("add-to-collection");
    };

    const onEditClick = () => {
      ElNotification({
        title: "Success",
        type: "success",
        customClass: "message",
        message: "Send to supervisor for approval"
      });
    };

    const onDeleteConfirm = () => {
      console.log("Delete");
    };

    return {
      onImageClick,
      onPlusClick,
      onEditClick,
      onDeleteConfirm,
      formatImageUrl
    };
  }
});

export default Component;
</script>

