
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";

import { formatImageUrl } from "@/utils/image";
import { CollectionFabrics } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCardEditable",
  props: {
    data: {
      type: Object as PropType<CollectionFabrics>,
      default: () => ({})
    }
  },
  setup(props, context) {
    const router = useRouter();

    const { data } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onPlusClick = () => {
      context.emit("add-to-collection");
    };

    const onEditClick = () => {
      ElNotification({
        title: "Success",
        type: "success",
        customClass: "message",
        message: "Send to supervisor for approval"
      });
    };

    const onDeleteConfirm = () => {
      console.log("Delete");
    };

    return {
      onImageClick,
      onPlusClick,
      onEditClick,
      onDeleteConfirm,
      formatImageUrl
    };
  }
});

export default Component;
